@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@font-face {
  font-family: nf-icon;
  src: url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.eot);
  src: url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.eot?#iefix)
      format("embedded-opentype"),
    url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.woff)
      format("woff"),
    url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.ttf)
      format("truetype"),
    url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.svg#nf-icon-v1-93)
      format("svg");
  font-weight: 400;
  font-style: normal;
}

[class*=" icon-"],
[class^="icon-"] {
  font-family: nf-icon;
  font-style: normal;
  font-weight: 400;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}

* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.lists {
  margin-top: -280px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

.loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.hide {
  display: none !important;
}

.movieRow {
  margin-bottom: 30px;
  position: relative;
}

.movieRow h2 {
  margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
  overflow-x: hidden;
  padding-left: 30px;
}

.movieRow--list {
  transition: all ease 0.8s;
}

.movieRow--item {
  display: inline-block;
  width: 280px;
  cursor: pointer;
}

.movieRow--item img {
  width: 100%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all ease 0.2s;
}

.movieRow--item img:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.movieRow--left,
.movieRow--right {
  position: absolute;
  width: 40px;
  height: 425px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
  cursor: pointer;
  opacity: 0;
  transition: all ease 0.2s;
}

.movieRow--left {
  left: 0;
}

.movieRow--right {
  right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
  opacity: 1;
}

.indicator-icon {
  font-size: 2em;
}

.icon-rightCaret:before {
  content: "\e867";
}

.icon-leftCaret:before {
  content: "\e868";
}

@media (max-width: 960px) {
  .movieRow--left,
  .movieRow--right {
    opacity: 1;
    display: none;
  }

  .movieRow h2 {
    margin-left: 16px;
  }

  .movieRow--listarea {
    padding-left: 16px;
  }

  .movieRow--list {
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    display: inline-block;
    width: 100% !important;
  }

  .movieRow--list::-webkit-scrollbar-track {
    background: #000;
  }
  .movieRow--list::-webkit-scrollbar-thumb {
    background: #babcbe;
    border-radius: 8px;
  }
  .movieRow--list::-webkit-scrollbar {
    border-radius: 8px;
    height: 6px;
  }

  .movieRow--item {
    width: 180px;
  }
}

.featured {
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent);
}

.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding-left: 30px;
  padding-bottom: 280px;
  padding-top: 70px;
}

.featured--name {
  font-size: 60px;
  font-weight: bold;
}

.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points {
  color: #46d369;
}

.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.featured--buttons {
  margin-top: 15px;
}

.featured--mylistbutton,
.featured--watchbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
}

.featured--mylistbutton:hover,
.featured--watchbutton:hover {
  opacity: 0.7;
}

.featured--watchbutton {
  background-color: #fff;
  color: #000;
}

.featured--mylistbutton {
  background-color: #333;
  color: #fff;
}

.featured--genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width: 960px) {
  .featured {
    height: 110vh;
    min-height: 300px;
  }

  .featured--name {
    font-size: 40px;
  }

  .featured--horizontal {
    padding-left: 16px;
  }

  .featured--info {
    font-size: 16px;
  }

  .featured--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }

  .featured--mylistbutton,
  .featured--watchbutton,
  .featured--genres {
    font-size: 14px;
  }
}

header {
  position: fixed;
  z-index: 999;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}

header.black {
  background: #141414;
}

.header--logo {
  height: 25px;
}

.header--logo img {
  height: 100%;
}

.header--user {
  height: 35px;
}

.header--user img {
  height: 100%;
  border-radius: 3px;
}

.icon-logoUpdate {
  text-decoration: none;
  font-size: 25px;
  color: #e50914;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.icon-logoUpdate:before {
  content: "\e5d0";
}

