header {
  position: fixed;
  z-index: 999;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}

header.black {
  background: #141414;
}

.header--logo {
  height: 25px;
}

.header--logo img {
  height: 100%;
}

.header--user {
  height: 35px;
}

.header--user img {
  height: 100%;
  border-radius: 3px;
}

.icon-logoUpdate {
  text-decoration: none;
  font-size: 25px;
  color: #e50914;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.icon-logoUpdate:before {
  content: "\e5d0";
}
